import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {LocationStrategy, PathLocationStrategy} from '@angular/common';

export const TDSAppRoute: Routes = [
	{path: '', pathMatch: 'full', redirectTo: 'security'},
	{path: 'security', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/security/security.module')['SecurityModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'tag', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/assetTags/asset-tags.module')['AssetTagsModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'asset', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/assetExplorer/asset-explorer.module')['AssetExplorerModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'fieldsettings', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/fieldSettings/field-settings.module')['FieldSettingsModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'importbatch', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/importBatch/import-batch.module')['ImportBatchModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'provider', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/provider/provider.module')['ProviderModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'credential', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/credential/credential.module')['CredentialModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'action', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/apiAction/api-action.module')['APIActionModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'datascript', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/dataScript/data-script.module')['DataScriptModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'user', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/user/user.module')['UserModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'dependencies', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/dependencies/dependencies.module')['DependenciesModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }},
	{path: 'license/admin', loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('../modules/licenseAdmin/license-admin.module')['LicenseAdminModule']);  }, function(e: any) {    reject({ loadChunkError: true, details: e });  });}) }}
];

@NgModule({
	exports: [RouterModule],
	providers: [{provide: LocationStrategy, useClass: PathLocationStrategy}],
	imports: [RouterModule.forRoot(TDSAppRoute, {enableTracing: true, onSameUrlNavigation: 'reload'})]
})

export class TDSAppRouteModule {
}